<template>
	<div class="box">
		<Header title="" />
		<div class="body">
			<ul>
				<li class="item">
					<span class="name">:</span>
					<span class="hide_1"><input type="text" placeholder=""></span>
				</li>
				<li class="item">
					<span class="name">:</span>
					<span class="hide_1"><input type="text" placeholder=""></span>
				</li>
				<li class="item">
					<span class="name">:</span>
					<span class="hide_1"><input type="text" placeholder=""></span>
				</li>
				<li class="item">
					<span class="name">:</span>
					<div class="select_1">
						<el-select v-model="select1" slot="prepend" placeholder="">
					      <el-option label="60" value="1"></el-option>
					      <el-option label="70" value="2"></el-option>
					      <el-option label="80" value="3"></el-option>
					    </el-select>
					</div>
				</li>
				<li class="item">
					<span class="name">:</span>
					<div class="select_1">
						<el-select v-model="select6" slot="prepend" placeholder="">
					      <el-option label="" value="1"></el-option>
					      <el-option label="" value="2"></el-option>
					      <el-option label="" value="3"></el-option>
					    </el-select>
					</div>
				</li>
				<li class="item">
					<span class="name">:</span>
					<span class="hide_1"><input type="text" placeholder=""></span>
				</li>
				<li class="item">
					<span class="name">:</span>
					<span class="hide_1"><input type="text" placeholder=""></span>
				</li>
				<li class="item_1">
				    <span class="name">:</span>
				    <div class="photo">
						<div class="icon">
							<i class="far fa-plus-square"></i>
						</div>
						<span class="hide_1">4</span>
					</div>
				</li>
				<li class="item ">
				    <p class="name_1 padding-bottom">:</p>
					<div class="check">
					<div class="padding-bottom">
						  <el-checkbox-group v-model="checkList">
						  	<span class="check_box"><el-checkbox label="24"></el-checkbox></span>
						  	<span class="check_box"><el-checkbox label=""></el-checkbox></span>
						  	<span class="check_box"><el-checkbox label=""></el-checkbox></span>
						  	<span class="check_box"><el-checkbox label=""></el-checkbox></span>
						  	<span class="check_box"><el-checkbox label=""></el-checkbox></span>
						  	
						    
						    <!-- <el-checkbox label=""></el-checkbox> -->
						  </el-checkbox-group>
					</div>
						  <span class="add_more"><i class="far fa-plus-square"></i></span>
						  <span></span>
					</div>
				</li>
			</ul>
			<div class="add_room">
				<span><i class="far fa-plus-square"></i></span>
				<span class="color"></span>
			</div>
			<div class="send">
				<button class="green_btn"></button>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		data(){
			return {
				  input3: '',
			      input4: '',
			      input5: '',
			      select1: '60',
			      select6: '',
			      checkList: ['24']
			}
		},
		methods: {

		}
	}
</script>
<style scoped lang="scss">
	@import "../../../common/css/common.scss";
	.box{
		width: 100%;
		.body{
			padding: 0 rem(16px);
			.item{
				position: relative;
				border-bottom: #e5e5e5 solid rem(1px);
				padding: rem(15px) 0;
				.check{
					.check_box{
						display: inline-block;
						width: 33%;
						padding: rem(3px) 0;
					}
				}
				.padding-bottom{
					padding-bottom: rem(10px);
				}
				.name{
					display: inline-block;
					width: 22%;
				}
				.select{
					margin-left: 5%;
			     }
				.hide{
					margin-left: 2%;
					color: #e5e5e5;
				}
				.hide_1{
					color: #e5e5e5;
					input{
						width: 60%;
						border: none;
						&::-webkit-input-placeholder { 
						    color:    #aaa;
						}
						&:-moz-placeholder { 
						    color:    #aaa;
						}
						&::-moz-placeholder { 
						    color:    #aaa;
						}
						&:-ms-input-placeholder { 
						    color:    #aaa;
						}
					}
				}
				.select_1{
					display: inline-block;
				}
				.add_more{
					color: #43c122;
				}
			}
			.room{
				margin-top: rem(19px);
				margin-bottom: rem(35px);
			}
			.item_1{
				border-bottom: #e5e5e5 solid rem(1px);
				padding: rem(15px) 0;
				.photo{
				padding-bottom: rem(4px);
				.icon{
					display: inline-block;
					border: #c9c9c9 solid rem(1px);
					padding: rem(32px) rem(35px);
					margin-left: 35%;
					border-radius: rem(9px);
					color: #43c122;
				 }
				.hide_1{
					color: #e5e5e5;
					margin-left: 5%;
				}
			 }
			}
			.add_room{
				text-align: center;
				padding: rem(20px) 0;
				color: #43c122;
				.color{
					color: #000;
				}
			}
		}

		.send{
			padding: rem(10px) rem(20px);
		}
	}
</style>


