<template>
	<div class="box">
		<Header title="" />
		<div class="body">
			<ul>
				<li class="item">
					<span class="name">:</span>
					<span class="select_1">
						<el-select v-model="select2" slot="prepend" placeholder="">
					      <el-option label="" value="1"></el-option>
					      <el-option label="" value="2"></el-option>
					      <el-option label="" value="3"></el-option>
					    </el-select>
					</span>
				</li>
				<li class="item">
					<span class="name">:</span>
					<span class="hide_1"><input type="text" placeholder=""></span>
				</li>
				<li class="item">
					<span class="name">:</span>
					<span class="hide_1"><input type="text" placeholder=""></span>
				</li>
				<li class="item">
					<span class="name">:</span>
					<span class="hide_1"><input type="text" placeholder=""></span>
				</li>
				<li class="item">
					<span class="name">:</span>
					<span class="hide_1"><input type="text" placeholder=""></span>
				</li>
				<li >
					<el-collapse v-model="activeNames" @change="handleChange">
						<el-collapse-item title="" name="1">
						    <p class="button">
						    	<span></span>
						    	<span class="open">
						    		<el-switch
									  v-model="value2"
									  active-color="#43c122"
									  inactive-color="#999999">
									</el-switch>
						    	</span>
						    </p>
						    <p class="select">
						    	<span>
						    		 <el-checkbox-group v-model="checkList">
									    <el-checkbox label="110%"></el-checkbox>
									  </el-checkbox-group>
						    	</span>
						    </p>
						    <p class="add">
						    	<span class="add_icon"><i class="fas fa-plus-square"></i></span>
						    	<span></span>
						    </p>
						 </el-collapse-item>
					</el-collapse>
				</li>
				<li class="item">
					<span class="name">:</span>
					<span class="select_1">
						<el-select v-model="select3" slot="prepend" placeholder="">
					      <el-option label="3" value="1"></el-option>
					      <el-option label="4" value="2"></el-option>
					      <el-option label="5" value="3"></el-option>
					    </el-select>
					</span>
				</li>
				<li class="item">
					<span class="name">:</span>
					<div class="select_1">
						<el-select v-model="select1" slot="prepend" placeholder="">
					      <el-option label="60" value="1"></el-option>
					      <el-option label="70" value="2"></el-option>
					      <el-option label="80" value="3"></el-option>
					    </el-select>
					</div>
				</li>
				<li class="item">
					<span class="name">:</span>
					<div class="select_1">
						<el-select v-model="select4" slot="prepend" placeholder="">
					      <el-option label="1" value="1"></el-option>
					      <el-option label="2" value="2"></el-option>
					    </el-select>
					</div>
				</li>
				<li class="item">
					<span class="name">:</span>
					<div class="select_1">
						<el-select v-model="select5" slot="prepend" placeholder="">
						<el-option label="1.2" value="1"></el-option>
					      <el-option label="1.3" value="2"></el-option>
					      <el-option label="1.5" value="3"></el-option>
					    </el-select>  
					    <span> &nbsp;</span>
					</div>
				</li>
				<li class="item">
					<span class="name">:</span>
					<div class="select_1">
						<el-select v-model="select6" slot="prepend" placeholder="">
					      <el-option label="" value="1"></el-option>
					      <el-option label="" value="2"></el-option>
					      <el-option label="" value="3"></el-option>
					    </el-select>
					</div>
				</li>
				<li class="item_1">
				    <span class="name">:</span>
				    <div class="photo">
						<div class="icon">
							<i class="far fa-plus-square"></i>
						</div>
						<span class="hide_1">4</span>
					</div>
				</li>
			</ul>
			<div class="add_room">
				<span><i class="far fa-plus-square"></i></span>
				<span class="color"></span>
			</div>
			<div class="send">
				<button class="green_btn"></button>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		data(){
			return {
				  input3: '',
			      input4: '',
			      input5: '',
			      select1: '60',
			      select2: '',
			      select3: '3',
			      select4: '1',
			      select5: '1.2',
			      select6: '',
			      // activeNames: ['1'],
			      value1: true,
                  value2: true,
                  checkList: ['110']
			}
		},
		methods: {
			handleChange(val) {
		        console.log(val);
		      }
		}
	}
</script>
<style scoped lang="scss">
	@import "../../../common/css/common.scss";
	.box{
		width: 100%;
		.body{
			padding: 0 rem(16px);
			p{
				.open{
					float: right;
				}
				.add_icon{
					color: #aaa;
					font-size: rem(14px);
				}
			}
			.item{
				position: relative;
				border-bottom: #e5e5e5 solid rem(1px);
				padding: rem(15px) 0;
				
				.check{
					.check_box{
						display: inline-block;
						width: 33%;
						padding: rem(3px) 0;
					}
				}
				.padding-bottom{
					padding-bottom: rem(10px);
				}
				.name{
					display: inline-block;
					width: 20%;
				}
				.select{
					margin-left: 5%;
			     }
				.hide{
					margin-left: 2%;
					color: #e5e5e5;
				}
				.hide_1{
					color: #e5e5e5;
					input{
						width: 60%;
						border: none;
						&::-webkit-input-placeholder { 
						    color:    #aaa;
						}
						&:-moz-placeholder { 
						    color:    #aaa;
						}
						&::-moz-placeholder { 
						    color:    #aaa;
						}
						&:-ms-input-placeholder { 
						    color:    #aaa;
						}
					}
				}
				.select_1{
					display: inline-block;
				}
				.add_more{
					color: #43c122;
				}
			}
			.room{
				margin-top: rem(19px);
				margin-bottom: rem(35px);
			}
			.item_1{
				border-bottom: #e5e5e5 solid rem(1px);
				padding: rem(15px) 0;
				.photo{
				padding-bottom: rem(4px);
				.icon{
					display: inline-block;
					border: #c9c9c9 solid rem(1px);
					padding: rem(32px) rem(35px);
					margin-left: 35%;
					border-radius: rem(9px);
					color: #43c122;
					z-index: 999;
				 }
				.hide_1{
					color: #e5e5e5;
					margin-left: 5%;
				}
			 }
			}
			.add_room{
				text-align: center;
				padding: rem(20px) 0;
				color: #43c122;
				.color{
					color: #000;
				}
			}
		}

		.send{
			padding: rem(10px) rem(20px);
		}
	}
</style>


